import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom';
import {
  Button,
  Card,
  CardBody,
  CardSubtitle,
  // CardText,
  CardTitle,
  Row,
} from 'reactstrap';

import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import RecentHack from '../../../components/common/RecentHack';
import { adminRoot } from '../../../constants/defaultValues';
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import AppError from '../../../helpers/AppError';

import IntlMessages from '../../../helpers/IntlMessages';
import { getCurrentUser } from '../../../helpers/Utils';
import { getAllHackathons } from '../../../redux/actions';

const ViewChallenges = ({
  match,
  accessToken,
  organizerHackathons,
  getAllHackathonsAction,
  loading,
  error,
}) => {
  const { id } = getCurrentUser();

  const history = useHistory();

  useEffect(() => {
    getAllHackathonsAction(accessToken, id);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (loading) return <div className="loading" />;

  if (error) throw new AppError(error);

  return (
    <>
      <Row>
        <Colxx xxs="10">
          <Breadcrumb heading="menu.challenges" match={match} />
        </Colxx>
        <Colxx xxs="2">
          <Button
            onClick={history.push(`${adminRoot}/challenges/explore`)}
            color="primary"
          >
            <IntlMessages id="challenges.explore" />
          </Button>
        </Colxx>
      </Row>

      <Separator className="mb-5" />
{/* 
      <Row>
        <Colxx xxs="12" md="8">
          <Card className="mb-4">
            <CardBody>
              <CardTitle>
                <h2 className="lead">EtherPunk 2021</h2>
                <p>An ETHIndia Hackathon.</p>
              </CardTitle>
              <CardSubtitle>
                <p>Registration Closes in 2 days.</p>
              </CardSubtitle>
            </CardBody>
          </Card>
        </Colxx>
        <Colxx xxs="12" md="4">
          <Card className="mb-4" color="warning">
            <CardBody>
              <CardTitle>
                <h2 className="lead">Organizing a Hackathon?</h2>
              </CardTitle>
              <CardText className="text-white">
                <p>
                  Okay Organize it. Lorem ipsum dolor sit amet, adipiscing elit,
                  eiusmod incididunt ut labore aliqua.
                </p>
              </CardText>
            </CardBody>
          </Card>
        </Colxx>
      </Row> */}

      <Row>
        <Colxx xxs="12">
          <Card className="mb-4">
            <CardBody>
              <CardTitle className="mb-0">
                <span className="h4">Your Hackathons</span>
              </CardTitle>
            </CardBody>
          </Card>
        </Colxx>
      </Row>

      {organizerHackathons.length ? (
        <Row>
          {organizerHackathons.map((item) => (
            <Colxx xxs="12" sm="6" md="4" key={item.id}>
              <RecentHack data={item} participated />
            </Colxx>
          ))}
        </Row>
      ) : (
        <Row>
          <Colxx xxs="12">
            <Card className="text-center">
              <CardBody>
                <CardTitle className="mb-0">You have applied to</CardTitle>

                <div className="display-1">{organizerHackathons.length}</div>
                <CardSubtitle>Hackathons</CardSubtitle>

                <div>
                  <Button
                    href={`${adminRoot}/challenges/explore`}
                    color="primary"
                  >
                    <IntlMessages id="challenges.explore-all" />
                  </Button>
                </div>
              </CardBody>
            </Card>
          </Colxx>
        </Row>
      )}
    </>
  );
};

const mapStateToProps = ({ authUser, hackathonData }) => {
  const { accessToken } = authUser;
  const { organizerHackathons, loading, error } = hackathonData;
  return { accessToken, organizerHackathons, loading, error };
};

export default connect(mapStateToProps, {
  getAllHackathonsAction: getAllHackathons,
})(ViewChallenges);
